import { Styled, jsx } from "theme-ui";
import { Tooltip } from "@material-ui/core";
import { jsPDF } from "jspdf";
import Moment from "react-moment";
import ProjectProps from "../@lekoarts/gatsby-theme-emma/components/projects";

var moment = require("moment");

const GeneratePdf = ({ projects }: ProjectsProps) => {
  return (
    <Styled.div className="w-100 text-center mt-4 mb-1">
      <Tooltip
        title={
          <div className="pdf-link-tooltip">
            Export projects to &nbsp;{" "}
            <span
              className="iconify"
              data-icon="grommet-icons:document-pdf"
              style={{ width: "1.5rem", height: "1.5rem" }}
              data-inline="true"
            ></span>
          </div>
        }
        arrow
      >
        <a
          className="download-pdf-anchor"
        >
          <span
            className="iconify"
            style={{ width: "6rem", height: "6rem", cursor: "pointer" }}
            data-icon="bi:download"
            data-inline="false"
          />
        </a>
      </Tooltip>
    </Styled.div>
  );
};

export default GeneratePdf;
